



.profile{
    height: 300px;
    width: 300px;
    border-radius: 150px;
    margin: 0px auto;
    margin-bottom: 0px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile img{
    height: 88%;
    width: 88%;
    border-radius: 150px;
    object-fit: cover;
}

.ring{
    height: 290px;
    width: 290px;
    position: absolute;
    border: 8px solid rgba(255, 255, 255, 0.568);
    border-radius: 150px;
    
}

.ring2{
    border: 8px solid rgba(255, 255, 255, 0.829);
    border-radius: 150px;
    border-top: 5px solid rgba(255, 255, 255, 0);
    border-bottom: 5px solid rgba(132, 38, 255, 0);
    

    height: 290px;
    width: 290px;
    position: absolute;
    transform: rotate(135deg);
    animation: 2.5s maddy infinite;
    box-shadow: 0 0 15px 4px rgba(255, 255, 255, 0.411); /* White box shadow */
    
}

@keyframes maddy {

    20%{
        transform: rotate(-45deg);
    }

    40%{
        transform: rotate(45deg);
    }

    60%{
        transform: rotate(100deg);
    }

    80%{
        transform: rotate(50deg);
    }


    100%{
        transform: rotate(60deg);
    }
    
}

.name{
    margin-top: 30px;
    text-align: center;
    color: white;
    font-size: 40px;
    font-family: monospace;
    font-weight: 700;
    
}

.about_me{
    padding: 20px;
    backdrop-filter: blur(20px); /* Blurring effect */
    -webkit-backdrop-filter: blur(20px); /* Safari support */
    color: white;
    border: 3px solid rgba(255, 255, 255, 0.247);
    border-left: 10px solid rgb(255, 0, 221);
    width: 90%;
    margin: 100px auto;
    font-size: 25px;
    box-shadow: 0 0 15px 4px rgba(255, 255, 255, 0.13);
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.116);
}

.hello_world{
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 20px;
}

.download_cv_button{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.219);
    padding: 15px;
    font-size: 20px;
    color: white;
    text-align: center;
    width: 230px;
    margin: 0px auto;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid white;
    transition: all 0.3s;
}

.download_cv_button:hover{
    background-color: rgba(255, 255, 255, 0.39);
    width: 260px;
    border-radius: 150px;
}

.download_cv_button img{
    height: 25px;
    width: 25px;
    margin-left: 10px;
    animation: download_but 1s infinite;
}

@keyframes download_but {
    0%{
        transform: translateY(0px);
    }
    25%{
        transform: translateY(-5px);
    }
    50%{
        transform: translateY(5px);
    }

    
}

.cards_containner{
    display: flex;
    margin: 100px auto;
    align-items: center;
    justify-content: center;
}

.card img{
    height: 50px;
    width: 50px;
    margin-bottom: 20px;
}

.card{
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.377);
    backdrop-filter: blur(15px); /* Blurring effect */
    -webkit-backdrop-filter: blur(15px); /* Safari support */
    border-radius: 10px;
    min-height: 300px;
    max-height: 300px;
    min-width: 400px;
    max-width: 400px;
    border-top: 7px solid rgb(255, 255, 255);
    font-size: 20px;
    text-align: center;
    color: white;
    font-weight: 600;
    position: relative;
    margin-right: 50px;
    transition: 0.3s;

}


.card:last-child{
    margin-right: 0px;
}

.card:hover{
    border: 10px solid rgb(255, 255, 255);
}

.card button{
    position: absolute;
    bottom: 20px;
    margin-top: 20px;
    padding: 10px;
    border: 3px solid white;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.151);
    color: white;
    font-size: 18px;
    font-weight: 600;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 60%;
    animation: card_button_shake 1s infinite;
}

@keyframes card_button_shake {
    0%{
        transform: translateX(-50%);
    }
    49%{
        transform: translateX(-50%);
    }
    50%{
        transform: translateX(-52%);
    }
    
    60%{
        transform: translateX(-48%);
    }
    70%{
        transform: translateX(-52%);
    }
    
    80%{
        transform: translateX(-48%);
    }
    90%{
        transform: translateX(-52%);
    }
    
    100%{
        transform: translateX(-48%);
    }
    
}




