@media screen and (max-width : 1400px) {
    .cards_containner{
        flex-direction: column;
    }

    .card{
        margin-bottom: 40px;
        margin-right: 0px;
    }
    
}

@media screen and (max-width : 500px) {
    .containner{
        padding-top: 50px;
    }
}


@media screen and (max-width : 500px) {
    .profile{
        height: 200px;
        width: 200px;
    }

    .ring, .ring2{
        height: 190px;
        width: 190px;
    }

    .name{
        font-size: 17px;
        margin-top: 10px;
    }

    .about_me{
        font-size: 12px;
        border-left: 6px solid rgb(255, 0, 221);
        padding: 10px;
        width: 95%;
        margin-top: 50px;
        margin-bottom: 0px;
    }

    .hello_world{
        font-size: 15px;
        margin-bottom: 10px;
    }

    .download_cv_button{
        font-size: 15px;
        padding: 10px;
        width: 200px;
        margin-top: 40px;
    }

    .download_cv_button img{
        width: 20px;
    }

    .cards_containner{
        width: 100%;
    }

    .card{
        padding: 10px;
        max-width: 95%;
        min-width: 95%;
    }

    .card button{
        padding: 10px;
        width: 180px;
    }
    
}