.back_button{
    height: 40px;
    width: 50px;
    margin: 40px;
}

.back_button img{
    height: 100%;
    width: 100%;
}

.projects_containner{
    padding: 20px;
    border: 4px solid white;
    border-radius: 20px;
    position: relative;
    width: 90%;
    margin: 0px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    backdrop-filter: blur(20px); /* Blurring effect */
    -webkit-backdrop-filter: blur(20px); /* Safari support */
    background-color: rgba(255, 255, 255, 0.137);
    padding-top: 100px;
    gap: 20px;
    margin-bottom: 100px;
}

.bag{
    width: 200px;
    height: 80px;
    position: absolute;
    border-radius: 150px;
    border-top: 10px solid white;
    top: -40px;
    left: 50%;
    transform: translateX(-50%);
}

.project_title{
    position: absolute;
    top: -15px;
    left: 50px;
    /* transform: translateX(-50%); */
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    font-size: 15px;
    font-weight: 600;
}

.project{
    background-color: rgba(255, 255, 255, 0.432);
    max-width: 350px;
    min-width: 350px;
    min-height: 300px;
    max-height: 300px;
    padding: 10px;
    border-radius: 20px;
    border: 3px solid white;
    position: relative;
    border-top: 10px solid white;
    margin-top: 50px;
}

.project_name{
    font-size: 20px;
    width: 100%;
    margin-bottom: 10px;
    border-bottom: 1px solid white;
    padding-bottom: 10px;
    font-weight: 600;
}

.project_des{
    font-size: 15px;
    color: white;
    font-weight: 600;
}

.projects_button{
    width: 350px;
    display: flex;
    justify-content: space-around;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 20px;
}

.project button{
    padding: 10px 15px;
    border: 2px solid white;
    background-color: rgba(255, 255, 255, 0.37);
    border-radius: 150px;
    color: rgb(68, 68, 68);
    font-weight: 700;
    transition: 0.2s;
}

.project button:hover{
    background-color: white;
}




