.address_containner{
    width: 700PX;
    margin: 50px auto;
    backdrop-filter: blur(50px);
    -webkit-backdrop-filter: blur(50px);
    border: 1px solid rgba(255, 255, 255, 0.178);
    border-radius: 10PX;
    padding: 30px;
    display: flex;
    align-items:  center;
    flex-direction: column;
    font-size: 25px;
    text-align: center;
    color: white;
    box-shadow: rgba(255, 255, 255, 0.17) 0px -23px 25px 0px inset, rgba(255, 255, 255, 0.15) 0px -36px 30px 0px inset, rgba(255, 255, 255, 0.1) 0px -79px 40px 0px inset, rgba(255, 255, 255, 0.06) 0px 2px 1px, rgba(255, 255, 255, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(255, 255, 255, 0.09) 0px 16px 8px, rgba(255, 255, 255, 0.09) 0px 32px 16px;
    border-top: 10px solid rgba(255, 255, 255, 0.932);
    position: relative;
}


.feel{
    font-size: 25px;
    font-weight: 700;
    margin-bottom: 20px;
    margin-top: 20px;
    border-bottom: 1px dotted rgba(255, 255, 255, 0.596);
    padding-bottom: 20px;
}

.address{
    margin-top: 50px;
    width: 100%;
    border-left: 5px solid white;
    border-right: 5px solid white;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.171);
    padding: 20px 10px;
    cursor: pointer;
}

.add_bold{
    font-weight: 700;
    font-size: 30px;
    color: rgb(246, 240, 255);
    margin-bottom: 10px;
}


.mail_me{
    width: 200px;
    background-color: red;
}

.address_containner button{
    padding: 10px 15px;
    background-color: rgba(255, 255, 255, 0.342);
    border: 2px solid white;
    border-radius: 5px;
    margin-top: 60px;
    color: white;
    font-weight: 700;
    font-size: 20px;
    transition: 0.3s;
}

.address_containner button:hover{
    transform: translateY(-10px);
}


.ball{
    position: absolute;
    width: 20px;
    height: 5px;
    background: white;
    top: 0px;
    left: 0px;
    border-radius: 20px;
    animation: ball_around 5s infinite linear;
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.8);
}


@keyframes ball_around {
    0% {
        top: 0px;
        left: 0px;
    }
    24%{
        transform: rotate(0deg);
    }
    25% {
        top: 0px;
        left: 98%;
        transform: translateX(-100%);
        transform: rotate(90deg);
    }
    49%{
        transform: rotate(90deg);
    }
    50% {
        top: 100%;
        left: 100%;
        transform: translate(-100%, -100%);
        transform: rotate(0deg);
    }
    74%{
        transform: rotate(0deg);
    }
    75% {
        top: 100%;
        left: 0px;
        transform: translateY(-100%);
        transform: rotate(90deg);
    }
    99%{
        transform: rotate(90deg);
    }
    100% {
        top: 0px;
        left: -10px;
        transform: rotate(0deg);
    }
}

