.footer_containner{
    backdrop-filter: blur(10px); /* Blurring effect */
    -webkit-backdrop-filter: blur(10px); /* Safari support */
    padding: 20px;
    height: 60px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.315);
    text-align: center;
    color: white;
    font-size: 18px;
    font-weight: 700;
    border: 2px solid rgba(255, 255, 255, 0.349);
    border-radius: 4px;
}

@media screen and (max-width : 600px) {
    .footer_containner{
        font-size: 13px;
    }
}