@media screen and (max-width : 900px) {
    .back_button{
        margin: 10px;
    }
    .back_button img{
        width: 20px;
        height: 20px;
        margin: 10px;
    }


    .projects_containner{
        width: 95%;
        border-width: 2px;
        padding-top: 20px;
        padding: 20px;
        gap: 0px;
        display: block;
    }

    .project_title{
        font-size: 10px;
        font-weight: 700;
        left: 25px;
    }

    .project{
        font-size: 7px;
        max-width: 95%;
        min-width: 95%;
        max-height: 200px;
        min-height: 200px;
        padding-top: 10px;
        padding: 8px;
        border-top: 6px solid white;
        text-align: center;
        margin: 40px auto;

    }

    .project_name{
        font-size: 15px;
        padding-bottom: 3px;
        font-weight: 700;
        color: white;
    }

    .project_des{
        font-size: 10px;
        color: black;
    }

    .bag{
        border-top: 5px solid white;
        width: 100px;
        height: 40px;
        top: -18px;
    }

    .projects_button{
        bottom: 10px;
    }

    .projects_button button{
        padding: 8px 18px;
    }

}