header{
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.158);
    backdrop-filter: blur(10px); /* Blurring effect */
    -webkit-backdrop-filter: blur(10px); /* Safari support */
    box-shadow: 0 4px 6px rgba(255, 255, 255, 0.1);
    padding: 15px 10px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    height: 70px;
    top: 0px;
    z-index: 2;
  }
  
  .logo{
      font-size: 30px;
      font-weight: 700;
      font-family: "Pattaya", serif;
      font-weight: 400;
      font-style: normal;
      cursor: pointer;
  }
  
  .nav_bar{
      display: flex;
      width: 50%;
      justify-content: flex-end;
      margin-right: 10px;
  
  }
  
  .nav_bar li{
      list-style-type: none;
      margin-left: 30px;
      font-weight: 700;
      position: relative;
  }

  .nav_bar li a{
    text-decoration: none;
    color: white;
    font-size: 20px;
  }

  .nav_bar li a::after{
    display: block;
    width: 0%;
    background-color: white;
    height: 3px;
    position: absolute;
    content: "";
    border-radius: 5px;
    transition: all 0.4s;
    
  }

  .nav_bar li a:hover::after{
    width: 100%;

    
  }

  .scroll_width_con{
    top: 70px;
  }

  .scroll_width{
    background: linear-gradient(150deg, #ec38bc, #fdeff9);
    background-color: red;
    border-radius: 150px;
    border: 1px solid rgba(0, 0, 0, 0.164);
}