*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: monospace;
}

body{
    background: linear-gradient(150deg, #2802ffbb, rgb(231, 49, 255), #ec38bc, #9a03c0bb, #ec38bc, #fdeff9);
    background-size: 300% 300%;
    animation: gradient_bg 4s infinite;
    min-height: 150vh;
}

@keyframes gradient_bg {
    0%{
        background-size: 300% 300%;
    }
    50%{
        background-size: 100% 100%;
    }
    100%{
        background-size: 300% 300%;
    }
    
}

.root{
    height: 100%;
}

.stone{
    height: 500px;
    width: 500px;
    background: linear-gradient(150deg, #ec38bc, #9a03c0bb, #fdeff9);
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70% ;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: fixed;
    z-index: -1;
}



.stone1{
    top: 200px;
    right: 50px;
    animation: stone1_moving 7s infinite;
    background-color: red;
}

@keyframes stone1_moving {
    0%{
        top: 200px;
    }
    50%{
        top: 300px;
        right: 80px;
    }
    100%{
        top: 200px;
    }

    
}

.stone2{
    transform: rotate(-40deg);
    left: 50px ;
    bottom: 200px;
    animation: stone2_moving 7s infinite;
    height: 450px;
    width: 450px;
}

@keyframes stone2_moving {
    0%{
        bottom: 200px;
    }
    50%{
        bottom: 300px;
        left: 80px;
    }
    100%{
        bottom: 200px;
    }

    
}


.containner{
    min-height: 150vh;
    backdrop-filter: blur(8px); /* Blurring effect */
    -webkit-backdrop-filter: blur(8px); /* Safari support */
    background-color: rgba(255, 255, 255, 0.089);
    padding-top: 100px;
    padding-bottom: 50px;
}

/* Falling animation */
@keyframes fall {
    0% {
        transform: translateY(-10vh) rotate(0deg);
        opacity: 1;
    }
    100% {
        transform: translateY(100vh) rotate(720deg);
        opacity: 0;
    }
}




